@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  font-family: 'Inter', sans-serif;
  transition: background-color 0.3s ease;
}

.fade-in {
  opacity: 0;
  animation: fadeIn 0.8s ease forwards;
}

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

.dark {
  background-color: #111827;
  color: white;
}

.splash-fade {
  animation: fadeSplash 0.5s ease-out 2.4s forwards;
  opacity: 1;
  pointer-events: none;
}

@keyframes fadeSplash {
  to {
    opacity: 0;
    display: none;
  }
}

.hover-effect {
  transition: transform 0.2s ease;
  transform-box: fill-box;
  transform-origin: center;
}

.hover-effect:hover {
  transform: scale(1.05);
}
